import React, { Component } from "react";
import API from "../../API/api";
import AppForm from "../AppForm/AppForm";
export default class EditPage extends Component {
  state = { found: false };
  fetchdata = async () => {
    try {
      const res = await API.put(`/pets/${this.props.id}`, {});
      console.log(res.data);
      // navigate to detail
    } catch (error) {
      console.log(error);
    }
  };
  async componentDidMount() {
    try {
      const res = await API.get(`/pets/${this.props.id}`);
      console.log(res.data);
      this.setState({ found: true, ...res.data });
    } catch (error) {
      console.log(error);
    }
  }
  render() {
    const display = () => {
      if (this.state.found) {
        return <AppForm edit={true} pet={this.state} />;
      }
    };
    return <div>{display()}</div>;
  }
}
