import React, { Component } from "react";
import API from "../../API/api";
import { navigate } from "@reach/router";
import Header from "../Header/Header";
export default class DetailPage extends Component {
  state = { found: false, liked: false };
  fetchData = async () => {
    try {
      const res = await API.get(`/pets/${this.props.id}`);
      console.log(res.data);
      this.setState({ found: true, ...res.data });
    } catch (error) {
      console.dir(error);
    }
  };
  componentDidMount() {
    this.fetchData();
  }
  handleClik = async () => {
    try {
      if (!this.state.liked) {
        const res = await API.patch(`/pets/${this.props.id}`);
        console.log(res.data);
        this.setState({ liked: !this.state.liked, ...res.data });
      }
    } catch (error) {
      console.dir(error);
    }
  };
  handleAdoption = async () => {
    try {
      const res = await API.delete(`/pets/${this.props.id}`);
      console.log(res.data);
      navigate("/");
    } catch (error) {
      console.dir(error);
    }
  };
  render() {
    const display = () => {
      if (this.state.found) {
        return (
          <>
            <button onClick={this.handleAdoption}>
              Adopt {this.state.name}
            </button>
            <h1>Detail About: {this.state.name}</h1>
            <h1>Pet Type: {this.state.petType}</h1>
            <h1>Likes: {this.state.likes}</h1>
            <h1>Skills:</h1>
            <ul>
              {this.state.skills.map((sk, i) => {
                return <li key={i}>{sk}</li>;
              })}
            </ul>

            <button onClick={this.handleClik}>like {this.state.name}</button>
          </>
        );
      }
    };
    return (
      <div>
        <Header linkDestination="/" linkText="back to home" />
        {display()}
      </div>
    );
  }
}
