import React, { Component } from "react";
import API from "../../API/api";
import TablePets from "../TablePets/TablePets";
import Header from "../Header/Header";
export default class Homepage extends Component {
  state = { pets: [] };
  fetchData = async () => {
    try {
      const res = await API.get("/pets");
      console.log(res.data);
      this.setState({ pets: res.data.pets });
    } catch (error) {
      console.dir(error);
    }
  };
  componentDidMount() {
    this.fetchData();
  }
  render() {
    return (
      <div>
        <Header linkDestination="/pets/new" linkText="add pet to shelter" />
        <TablePets pets={this.state.pets} />
      </div>
    );
  }
}
