import React, { Component } from "react";
import { navigate } from "@reach/router";
import API from "../../API/api";
import Header from "../Header/Header";
import "./AppForm.css";
export default class AppForm extends Component {
  state = {
    name: "",
    petType: "",
    description: "",
    skillOne: "",
    skillTwo: "",
    skillThree: "",
    errors: [],
  };
  componentDidMount() {
    if (this.props.edit) {
      const { skills, ...restOfPet } = this.props.pet;
      let ob = { skillOne: "", skillTwo: "", skillThree: "" };
      for (let i = 0; i < 3; i++) {
        if (skills[i]) {
          if (i === 0) {
            ob.skillOne = skills[i];
          }
          if (i === 1) {
            ob.skillTwo = skills[i];
          }
          if (i === 2) {
            ob.skillThree = skills[i];
          }
        }
      }

      this.setState({ ...ob, ...restOfPet });
    }
  }
  handleChange = (e) => {
    this.setState({ [e.target.name]: e.target.value });
  };
  handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const { skillOne, skillTwo, skillThree, ...restOfState } = this.state;
      const skills = [skillOne, skillTwo, skillThree];
      const reqBody = {
        skills: skills.filter((sk) => {
          return sk.length !== 0;
        }),
        ...restOfState,
      };
      if (!this.props.edit) {
        const response = await API.post("/pets/new", reqBody);
        console.log(response.data);
        navigate("/");
      } else {
        const response = await API.put(`/pets/${this.props.pet._id}`, reqBody);
        console.log(response.data);
        navigate(`/pets/${this.props.pet._id}/detail`);
      }
    } catch (error) {
      let obj = error.response.data.errors;
      this.setState({ errors: Object.entries(obj) });
    }
  };
  render() {
    return (
      <div>
        <Header linkDestination="/" linkText="back to home" />
        <form onSubmit={(this, this.handleSubmit)} id="app-form">
          <span id="app-form-top">
            <span id="app-form-left" className="form-data">
              <input
                name="name"
                type="text"
                onChange={this.handleChange}
                placeholder="Name"
                value={this.state.name}
              />
              <input
                name="petType"
                type="text"
                onChange={this.handleChange}
                placeholder="Pet Type"
                value={this.state.petType}
              />
              <input
                name="description"
                type="text"
                onChange={this.handleChange}
                placeholder="Description"
                value={this.state.description}
              />
            </span>
            <span id="app-form-right" className="form-data">
              <h1>Skills:Optional</h1>
              <input
                name="skillOne"
                type="text"
                onChange={this.handleChange}
                placeholder="Skill 1"
                value={this.state.skillOne}
              />
              <input
                name="skillTwo"
                type="text"
                onChange={this.handleChange}
                placeholder="Skill 2"
                value={this.state.skillTwo}
              />
              <input
                name="skillThree"
                type="text"
                onChange={this.handleChange}
                placeholder="Skill 3"
                value={this.state.skillThree}
              />
            </span>
          </span>

          <input
            type="submit"
            value={this.props.edit ? "Edit" : "create"}
            id="last-form-button"
          />
        </form>
        <span>
          {this.state.errors.length ? (
            <ul>
              {this.state.errors.map((er, i) => {
                return <li key={i}>{er[1].message}</li>;
              })}
            </ul>
          ) : null}
        </span>
      </div>
    );
  }
}
